import React, { Component } from "react";
import Aboutme from "./aboutme";
import Reveal, { RevealRight, RevealOnly } from "./Reveal";
import Github from "../Pics/github.png";
import Linkedin from "../Pics/linkedin.png";
import Contact from "./Contact";

class Coverpage extends Component {
  constructor() {
    super();
    this.state = {
      isClicked: false,
      isHover: false
    };
  }

  toggle_ = () => {
    this.setState({ isClicked: !this.state.isClicked });
  };
  hover_ = () => {
    this.setState({ isHover: !this.state.isHover });
  };

  render() {
    const { isClicked, isHover } = this.state;
    return (
      <div className="together">
        \_(ツ)_/
        <div className="container">
          <div
            style={{
              background: "tomato",
              color: "white"
            }}
          >
            <Contact></Contact>
          </div>
          <div
            style={{ background: "tomato", color: "white" }}
            onMouseEnter={this.hover_}
            onMouseLeave={this.hover_}
          >
            <div className="first ">
              <Reveal>
                <h1>It's Anthony Hsia.</h1>

                <RevealOnly>
                  <h3>A computer scientist.</h3>
                </RevealOnly>
              </Reveal>
            </div>
            <div className="second">
              <RevealOnly>(:</RevealOnly>
            </div>
            {isHover ? (
              <span className="arrow-down">(Scroll u noob ᕙ(⇀‸↼‶)ᕗ)</span>
            ) : (
              <div></div>
            )}
          </div>
          <div className="click-me" onClick={this.toggle_}>
            {isClicked ? (
              <RevealOnly>
                <Aboutme></Aboutme>
              </RevealOnly>
            ) : (
              <Reveal>
                <h3>ABOUT ME</h3>
              </Reveal>
            )}
          </div>
          <div style={{ background: "sandybrown", color: "white" }}>
            <Reveal>
              <h3>FOOD</h3>
            </Reveal>
          </div>
          <div style={{ background: "lightcoral", color: "white" }}>
            <Reveal>
              <h3>ESPORTS</h3>
            </Reveal>
          </div>
        </div>
        <div className="rightside_container">
          <div className="rightside_items">
            <div></div>
            <div>
              <a href="https://github.com/ahsia3" target="_blank">
                <img src={Github} width="25px" height="25px" alt="github"></img>
              </a>
            </div>
            <br />
            <div>
              <a
                href="https://www.linkedin.com/in/anthonyhsia22/"
                target="_blank"
              >
                <img
                  src={Linkedin}
                  width="25px"
                  height="25px"
                  alt="Linkedin"
                ></img>
              </a>
            </div>
            <div></div>
          </div>
        </div>
      </div>
    );
  }
}

export default Coverpage;
